import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/mochila_.jpg'
import thumb02 from '../assets/images/thumbs_ML/mochila 2.jpg'
import thumb03 from '../assets/images/thumbs_ML/mochila 3.jpg'
import thumb04 from '../assets/images/thumbs_ML/mochila 4.jpg'
import thumb05 from '../assets/images/thumbs_ML/mochila 5.jpg'

import full01 from '../assets/images/fulls/mochila.jpg'
import full02 from '../assets/images/fulls/mochila 2.jpg'
import full03 from '../assets/images/fulls/mochila 3.jpg'
import full04 from '../assets/images/fulls/mochila 4.jpg'
import full05 from '../assets/images/fulls/mochila 5.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jard√≠n De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '3', src: full03, thumbnail: thumb03, caption: 'Photo 3', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '4', src: full04, thumbnail: thumb04, caption: 'Photo 4', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'},
    { id: '5', src: full05, thumbnail: thumb05, caption: 'Photo 5', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Mochila de Tela</h2>
                            <h4>$65</h4> 
                        </header>
                        <p>DESCRIPCION: <br />
                        * Mochila de Tela Personalizada con motivos a eleccion.<br />
                        * Consta de: <br />
                        - Mochila de Tela de 25 x 35 cm<br />
                        Medidas Aproximadas.<br />
                        Todo Personalizado a eleccion con imagen y/o foto, nombre. <br />
                        * Materiales: tropical mecanico.</p>

                        <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-a0f95f98-a938-4b75-9f5b-a1eb16ab68ed" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br />
                       
                        <Link to="/">Volver</Link>

                        <br /><br />
                    </section>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex



